                    Qua câu chuyện này ta rút ra được bài học, của cải vật chất dù nhiều đến đâu cũng sẽ có ngày hết đi
                    nếu ta không biết chí thú làm ăn. Bên cạnh đó cũng là lời nhắn nhủ đến những ai đã từng có thói hư tật
                    xấu nhưng nếu biết hối cãi thành tâm cũng sẽ được tha thứ và chấp nhận.&nbsp;<br>
                    Nguồn: Sưu tầm
                  </div>
                </div>
                <!-- ket thuc phai chan-->
                <div class="page" :class="getCl(8)" :style="getZindex(8)"   @click="denketthuc()">
                  <KetThuc></KetThuc>
                </div>
              </div>
  </div>

  </b-row>
  
  </b-col>

  <b-col cols="2">
    <RightCom></RightCom>
  </b-col>

  </b-row>
  
</div></template>
<script>

import KetThuc from '../components/KetThuc.vue'
import Modau from '../components/Modau.vue'
import {Clicks} from '../data/Click' 
import LeftCom from '../components/Lefts.vue'
import RightCom from '../components/Right.vue'
export default {
  name: 'gai-ngoan-day-chong',
  data: () => ({
    anh: 'https://cotich.net/uploads/news/a_1437515550_gai-ngoan-day-chong.jpg',
    per: -1,
    next: -1,
    ht: -1,
    ht2: -1,
    arrClassScc: ["", "", "", "", "", "", "", "", "", "", ""],
    kkk: 1
  }),
  metaInfo: {
    title: 'Gái ngoan dạy chồng - Cổ tích thế tục chọn lọc',

    meta: [
      { charset: 'utf-8' },
      { name: 'Keywords', content: 'Gái ngoan dạy chồng' },
      { name: 'Description', content: 'Gái ngoan dạy chồng là câu chuyện kể về một chàng trai trong gia đình giàu có chỉ thích ăn chơi lêu lỏng. Sau khi có vợ vẫn giữ tật xấu này cho đến khi toàn bộ gia tài đều mất đi vì bài bạc. May mắn thay nhờ người vợ tu chí làm ăn và có những bài học thích đáng mà cuối cùng anh cũng biết ăn năng hối cãi. ' },
      { name: 'thumbnail', content: 'https://cotich.net/uploads/news/a_1437515550_gai-ngoan-day-chong.jpg' },

      { itemprop: 'name', content: 'Truyện cổ tích' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1, maximum-scale=1' },
      { name: 'language', content: 'Vietnamese,vn' },
      { name: 'geo.region', content: 'VN' },
      { property: 'article:tag', content: 'Truyện cổ tích, Chuyện cổ tích' },
      { name: 'robots', content: 'index,follow' },
    ]
  },
  components: {
    LeftCom,
    RightCom,
    Modau,
    KetThuc
  },
  methods: {
     denmodau(){
      Clicks.clickmodau();
    },
    denketthuc(){
      Clicks.clickketthuc();
    },
    clicks(vts) {
      this.ht = vts + 1;
      this.ht2 = vts;
      this.per = vts - 1;
      this.next = vts + 1;
      if (this.ht % 2 === 0) {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], false);
        if (this.per > 0) {
          this.arrClassScc[this.per] = this.setClass(this.arrClassScc[this.per], false);
        }
      } else {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], true);
        if (this.next < this.arrClassScc.length) {
          this.arrClassScc[this.next] = this.setClass(this.arrClassScc[this.next], true);
        }
      }
      this.kkk++;
    },
    setClass(sClass, add) {
      var f = 'flipped';
      if (add) {
        if (sClass.indexOf(f) < 0) {
          sClass = sClass + " " + f;
        }
      } else {
        if (sClass.indexOf(" " + f) > 0) {
          sClass = sClass.replace(" " + f, "");
        } else
          if (sClass.indexOf(f) > 0) {
            sClass = sClass.replace(f, "");
          }
      }
      return sClass;
    },
    getCl(vt) {
      return this.arrClassScc[vt];

    },
    getZindex(vt) {
      if (vt % 2 === 0) {
        return "z-index: " + (4 - vt) + ";"
      } else {
        return "";
      }
    }
  },
  created() {
  }
}
</script>

<style>@import '../app.css';
@import 'truyen.css';</style>
