var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"bgs",style:({ backgroundImage: 'url(bg/bglon2.webp),url(bg/bglon1.webp)' })},[_c('b-row',{staticClass:"fh"},[_c('b-col',{attrs:{"cols":"2"}},[_c('LeftCom')],1),_c('b-col',{attrs:{"cols":"8"}},[_c('b-row',{staticClass:"bookview"},[_c('div',{staticClass:"book"},[(_vm.kkk > 0)?_c('div',{staticClass:"pages",attrs:{"id":"pages"}},[_c('div',{staticClass:"page",class:_vm.getCl(0),style:(_vm.getZindex(0)),on:{"click":function($event){return _vm.clicks(0)}}},[_c('div',{staticClass:"book",style:({ backgroundImage: 'url(bg/a.png), url(' + _vm.anh + ')' })})]),_c('div',{staticClass:"page2",class:_vm.getCl(1),style:(_vm.getZindex(1))},[_c('Modau')],1),_c('div',{staticClass:"page scolls",class:_vm.getCl(2),style:(_vm.getZindex(2)),on:{"click":function($event){return _vm.clicks(2)}}},[_c('div',{staticClass:"box-content"},[_c('h2',{staticStyle:{"text-align":"center"}},[_c('span',{},[_vm._v("Gái ngoan dạy chồng - Cổ tích thế tục chọn lọc")])]),_c('h3',[_c('em',[_c('span',{},[_vm._v("Gái ngoan dạy chồng là câu chuyện kể về một chàng trai trong gia đình giàu có chỉ thích ăn chơi lêu lỏng. Sau khi có vợ vẫn giữ tật xấu này cho đến khi toàn bộ gia tài đều mất đi vì bài bạc. May mắn thay nhờ người vợ tu chí làm ăn và có những bài học thích đáng mà cuối cùng anh cũng biết ăn năng hối cãi.")])])])])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(3),style:(_vm.getZindex(3)),on:{"click":function($event){return _vm.clicks(3)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Ngày xưa có một người đàn ông giàu, vợ chết sớm, chỉ có một người con trai. Đứa con xấu nết, đần độn, lại là tay chơi bời lêu lổng không chịu học hành hay làm ăn gì cả. "),_c('br'),_vm._v(" Thấy con không lo nối nghiệp nhà, người nhà giàu rất buồn phiền, biết rằng của cải của mình sẽ có một ngày đội nón ra đi mà thôi. Bởi vậy, ông bèn tính chuyện kiếm cho con một người vợ khôn ngoan đảm đang, để may ra nó sẽ ngăn chặn tay chồng, bảo vệ một phần nào cơ nghiệp."),_c('br'),_c('br'),_vm._v(" Nghĩ vậy, ông cất công đi khắp nơi tìm kiếm, nhưng đi đã nhiều nơi mà vẫn chưa thấy một người nào vừa ý."),_c('br'),_c('br'),_vm._v(" Một hôm, đến một vùng kia, nhân mỏi chân, ông nghỉ lại một gốc đa bên đường. Ông bỗng thấy ở một cây táo gần đấy có một bọn trẻ đang tranh nhau trẩy ăn . Một chốc sau, một cô gái tuổi chừng đôi tám, cũng đến hái táo. Ông mới lân la lại gần hỏi xin ăn ."),_c('br'),_c('br'),_vm._v(" Lúc này, táo chín đã bị bọn người trước hái hết, chỉ còn những quả xanh, nhưng cô gái cũng cố chọn lấy những quả ương ương đưa cho khách."),_c('br'),_c('br'),_vm._v(" Thấy cô gái tốt bụng, ông ta nghĩ : “Sởi lởi, trời gởi của cho, quăn co, trời gò của lại. Chỉ có người nào tốt bụng với mọi người mới xứng đáng được hưởng giàu có sung sướng”."),_c('br'),_c('br'),_vm._v(" Bởi vậy, ông mới tìm đến nhà cô gái, giả làm một người lỡ độ đường, xin nghỉ trọ một tối. Và rồi ông được gia đình cô gái ân cần tiếp đãi."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(4),style:(_vm.getZindex(4)),on:{"click":function($event){return _vm.clicks(4)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Để thử xem cô gái có khôn ngoan không, ông lân la làm quen với nàng. Khi biết cô sắp đi chợ, ông đưa ra một quan tiền nhờ mua hộ cho mình “một nắm gió, một bó lửa”. Cô gái chẳng nói chẳng rằng, mua về cho ông một cái quạt và một con dao đánh lửa. Thấy thế, ông thầm khen ngợi, nhưng vẫn định thử thêm cho biết."),_c('br'),_c('br'),_vm._v(" Qua ngày hôm sau, ông dậy sớm, giở tay nải đưa cho cô mấy bát gạo nếp, nhờ nàng thổi giúp cho mình một nồi vừa cơm vừa bánh để ăn và đem đi ăn đường. Cô gái không từ chối, vội lấy gạo ra vo . Trước khi cho vào nồi, nàng bớt lại một ít giã làm bột vắt bánh rồi hấp luôn vào cơm . Khi nàng bưng ra, ông già lấy làm vừa ý, cho là con người đó đủ cả đức hạnh, khôn ngoan, đảm đang ít có. Bèn quyết định trở về sửa soạn lễ hỏi cho con trai làm vợ."),_c('br'),_c('br'),_vm._v(" Đứa con trai của ông từ ngày có vợ lại càng lêu lỏng; hắn thường bỏ nhà đi đánh đàn, đánh đúm với bọn vô lại, làm cho ông hết sức buồn. Và điều làm ông lo lắng nhất là hắn thỉnh thoảng lại trộm tiền của ông, khi dăm bảy quan, khi một vài vác, tung vào cuộc đỏ đen. Mặc dù ông đánh đập, mặc dù vợ hắn khuyên lơn, nhưng hắn chứng nào vẫn tật ấy. Dần dần ông buồn phiền thành bệnh. Một hôm, biết mình sắp chết, ông gọi con dâu đến bên giường dặn nhỏ :"),_c('br'),_c('br'),_vm._v(" – Này cha đã gần đất xa trờị Chồng con là một thằng “Phá gia chi tử”, cơ nghiệp này chỉ còn một sớm một chiều mà thôi. Cha rất thương con xấu số. Từ lâu cha làm ăn dành dụm, có để được một hũ vàng chôn ở sau vườn. Vậy cha cho riêng con hũ vàng đó, đừng cho chồng con biết. Sau này chồng con có thật sự ăn năn thì giúp cho nó làm lại cuộc đờị"),_c('br'),_c('br'),_vm._v(" Đứa con trai ông sau khi người cha qua đời, lại càng chơi bời mặc sức. Vợ hết khuyên lơn đến cầu khẩn, hắn chẳng những đã không nghe lại còn phũ phàng với vợ. Mỗi lần thua bạc, hai người lại càng xô xát. Nhiều lần vì vợ cản trở, hắn đánh đập vợ không tiếc tay và làm nhục nàng trước mặt mọi ngườị. Một hôm để khỏi vướng, hắn viết giấy cho vợ đi lấy chồng khác rồi đuổi nàng ra khỏi cửa."),_c('br'),_c('br'),_vm._v(" Từ đó, hắn phỉ chí tung hoành không một ai dám cản. Quả như lời đoán của bố hắn, mấy chục mẫu ruộng đều lần lượt “nướng” vào sòng bạc. Hết ruộng vườn tới nhà ở, chẳng bao lâu tất cả cơ nghiệp mấy đời lưu truyền lại đều sạch sành sanh . Cuối cùng không một đồng dính túi, không một nghề cầm tay, hắn đành bỏ làng mạc quê quán, đi lang thang khắp đầu đường xó chợ, ngửa tay xin ăn qua ngày."),_c('br'),_c('br'),_vm._v(" Còn về người đàn bà sau khi bị chồng đuổi, mới đổi tên, tìm đến trấn thành mở một ngôi hàng nước. Sau ít lâu, kiếm được một số nhỏ, nàng bắt đầu buôn hàng tấm. Số vốn của nàng ngày một lớn dần. Một hôm, gặp hai em bé gái mồ côi đi ăn xin, nàng thương tình đưa về nuôi làm con, coi như ruột thịt. Cuộc đời dần dần nở hoa trước mắt nàng. Trong một dịp đi tìm kiếm củi, hai con nàng nhặt được một khúc gỗ mục, về chẻ ra thấy có mấy thoi vàng. Có vốn lớn lại có tài kinh doanh, nên chẳng bao lâu, nàng trở nên giàu có, nổi tiếng trong trấn. Tiền bạc tuôn về như nước. Tuy sống sung sướng nhưng nàng vẫn ở một thân một mình. Thấy nàng giàu có, nhiều kẻ ngấp nghé muốn thành hôn, nhưng người đàn bà ấy nhất thiết từ chối mọi lời đường mật. Mặc dù người chồng bạc bẽo và mặc dầu mười lăm năm xa cách, nàng vẫn không quên được tình xưa nghĩa cũ. Đã nhiều lần nàng thuê người lần đi các chợ búa phố phường dò hỏi nhưng tin tức của chồng càng hỏi, càng bặt tăm ."),_c('br'),_c('br'),_vm._v(" Năm ấy, sau mấy tháng hạn, lúa khoai chết mòn trên những cánh đồng nứt nẻ. Giá gạo cứ lên vùn vụt. Ngoài đường, người đi xin ăn kéo từng đoàn. Người đàn bà lúc này đã là bà chủ tiệm. Bà xin phép quan trên cho mình đem tiền gạo ra phát chẩn cho kẻ khó. Làm như thế, nàng còn mong một khi thấy yết thị dán khắp thôn xóm thì chồng mình tất sẽ lần mò về, nếu hắn còn sống. Y như thế thật, ngày bắt đầu phát, nàng đã thấy bóng dáng của chồng ngồi ở hàng cuối đội quân lĩnh chẩn. Đúng là hắn. Từ ngày bắt đầu cầm bị gậy đến nay, hắn vẫn chưa có cách gì để sống khá hơn và đỡ hèn hạ hơn trước. Bây giờ nghe nói có phát chẩn, hắn vội mò đến đây và ngồi đầu hàng về phía trái. Thế nhưng khi phát, những người giúp việc cho bà chủ hiệu lại được lệnh phát từ phía phải trước. Khi sắp sửa đến lượt hắn thì bọn họ tự nhiên nói lớn :"),_c('br'),_c('br'),_vm._v(" – Hôm nay đã hết gạo, mời bà con về đợi đến ngày mai !"),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(5),style:(_vm.getZindex(5)),on:{"click":function($event){return _vm.clicks(5)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Hắn buồn bực trở ra . Qua ngày sau, hắn cố tìm đến thật sớm, ngồi vào đầu hàng bên phải. Nhưng hắn không ngờ, những người phát chẩn hôm nay lại bắt đầu phát từ phía bên kia . Lúc sắp phát đến hắn thì chúng lại giơ thúng không lên :"),_c('br'),_c('br'),_vm._v(" – Hôm nay thế là lại hết gạọ Bà con hãy đợi đến mai ."),_c('br'),_c('br'),_vm._v(" Hắn thở than cho số đen đủi, lần trở ra về. Qua hôm sau, lại mò đến thật sớm. Lần này hắn len vào ngồi đúng chính giữa đội quân lĩnh chẩn. Trong bụng hắn nghĩ lần nầy thì không để mất phần được. Nhưng đến giờ phát, hắn không ngờ người nhà của bà chủ hiệu hôm nay lại phát hai đầu phát lại và cuối cùng người không được gì cả, vẫn lại là hắn. Ba lần hỏng cả ba, hắn rất ngao ngán, bèn đánh liều tìm đến dinh cơ bà chủ để xin ăn . Gặp hai đứa con gái nuôi của vợ, hắn ngả nón kêu van hết lời. Ở trong nhà người đàn bà nhìn ra biết là chồng, đã do mưu của mình mà đến đây, bèn sai người hầu ra hỏi, xem hắn có biết làm việc gì không để thuê mượn. Nghe hỏi thế, hắn vội trả lời :"),_c('br'),_c('br'),_vm._v(" – Xin ông bẩm với bà lớn rủ lòng thương, cho tôi được ở hầu bà, rửa bát, quét nhà, mọi việc tôi đều xin hết sức. Chỉ cho tôi ăn ba miếng là đủ rồi !"),_c('br'),_c('br'),_vm._v(" Người nhà trở ra cho hắn biết bà chủ nhận lời. Từ đó, hắn chăm chỉ làm lụng, cố làm vừa lòng chủ. Nhưng chung quy hắn vẫn không biết chủ chính là vợ cũ của mình. Về phần người vợ cũng không hề để lộ một tí cho hắn biết, chỉ dặn hai con và người nhà đối đãi tử tế mà thôi . Sau một thời gian, thấy hắn chịu khó làm ăn, người đàn bà mừng lắm. Một hôm nàng cho gọi hắn lên nhà hỏi xem có biết chữ nghĩa gì không ! Hắn đáp :"),_c('br'),_c('br'),_vm._v(" – Tôi lúc nhỏ được đi học có biết ít nhiềụ"),_c('br'),_c('br'),_vm._v(" – Vậy từ mai trở đi, anh không hầu hạ nữa, cho anh ở gian nhà khách dạy đám trẻ học tôi sẽ trả mỗi năm ba mươi quan tiền."),_c('br'),_c('br'),_vm._v(" Nghe nói, hắn tưởng không có gì sung sướng hơn thế nữa, cảm thấy lòng nhân đức của bà chủ đối với mình bằng trời bằng biển, vội nhận lời ngay . Từ đó, hắn đóng vai thầy đồ cố sức làm cho chủ tin cậy. Nhưng người vợ vẫn thử mãi không thôi ."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(6),style:(_vm.getZindex(6)),on:{"click":function($event){return _vm.clicks(6)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Một lần, gặp ngày tết, bà chủ sai lấy tiền ra cho kẻ hầu người hạ. Nàng nói :"),_c('br'),_c('br'),_vm._v(" – Ta cho mỗi người năm quan, hãy mang đi đánh bạc cho vui, nếu hết sẽ cho thêm . Thầy đồ ta cũng được năm quan tiền. Nhưng trong khi mọi người đem tiền nướng vào xóc đĩa, bài mười, thì trái lại, hắn mang nguyên vẹn số tiền đó về gửi cho chủ. Bà chủ hỏi :"),_c('br'),_c('br'),_vm._v(" – Tại sao anh không thích đánh bạc ? Hắn trả lời :"),_c('br'),_c('br'),_vm._v(" – Bẩm bà, tôi ngày xưa, vì cờ bạc mà đến nông nỗi này. Cho nên bây giờ buộc chỉ cổ tay thề rằng không đụng đến nó."),_c('br'),_c('br'),_vm._v(" Thế rồi luôn miệng, hắn kể hết cho chủ nghe, từ cuộc đời cũ có ruộng nhiều, có vợ ngoan như thế nào, rồi bán ruộng đuổi vợ ra sao, cho đến lang thang đói rách, và ngày nay đã ăn năn hối lỗi v.v… Bà chủ hỏi :"),_c('br'),_c('br'),_vm._v(" – Anh còn thương vợ nữa không ? Hắn rầu rĩ :"),_c('br'),_c('br'),_vm._v(" – Tôi đã nhiều lần dò tìm mà không thấỵ"),_c('br'),_c('br'),_vm._v(" – Nghe anh nói tôi rất thương tình. Vậy tôi cho anh năm quan để anh đi tìm vợ. Nếu hết tiền mà vẫn chưa thấy, anh cứ về đây, tôi sẽ cho thêm mà tìm cho ra."),_c('br'),_c('br'),_vm._v(" Hắn mừng rỡ vâng vâng, dạ dạ, mang tiền đi tìm. Nhưng sau ba tháng trở về với bộ mặt thiểu não, hắn cho chủ biết không hề thấy tung tích đâu cả, chắc là vợ hắn đã chết."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(7),style:(_vm.getZindex(7)),on:{"click":function($event){return _vm.clicks(7)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Từ đấy, vợ thấy chồng chí tình, lại có lòng tu tỉnh, nên rất mừng. Nhưng nàng vẫn chưa ra mặt vội, chỉ an ủi hắn hãy ở lại đây, may ra sẽ có ngày hội ngộ."),_c('br'),_c('br'),_vm._v(" Một hôm, nhân ngày giỗ cha chồng, bà chủ nhờ thầy đồ chép bài văn tế. Hắn ta ngạc nhiên và bội phần mừng rỡ khi thấy bài vị của tổ tiên 